<script>
	import {
		mapState
	} from "vuex";
	import {
		business
	} from "@/mixin/business";
	import {
		uploadFile
	} from "@/mixin/uploadFile";
	import Canvas from "@/components/Canvas.vue";
	import Signature from "../components/Signature.vue";
	export default {

		name: "gaochu",
		mixins: [business, uploadFile],
		components: {
			Signature,
			Canvas
		},

		computed: mapState({
			reason: state => state.duanlu.reason,
			endangerSign: state => state.duanlu.endangerSign,
			workCharger: state => state.duanlu.workCharger
		}),

		data() {

			return {
				isShowAction: false,
				checked: [{
					checked: false,
					image: ""
				}],
				signatureShow: false,
				mask: [0, 1],
				value:5,
				light:0,
				Alight: 0,
				
			}

		},
		methods: {

			// Popup
			openAction() {
				this.isShowAction = true;
			},
			// Popup
			closeAction() {
				this.isShowAction = false;
			},
			// 
			showSignature(index) {
				console.log("index: ", index);
				console.log("");
				this.xuhao = index;
				this.signatureShow = true;
			},
			// 
			signatureCancel(index) {
				console.log("index: ", index);
				console.log("");
				this.checked[index].checked = false;
				this.checked[index].img = "";
			},
			saveCanvas(e) {
				this.signatureShow = false;
				this.checked[this.xuhao] = {
					checked: false,
					img: ""
				};
				this.checked[this.xuhao].img = e;
				console.log("signatureShow: ");
			},
			cancelCanvas() {
				this.checked[this.xuhao].checked = false;
				this.checked[this.xuhao].img = "";
				this.signatureShow = false;
			}

		}

	}
</script>

